let do_uwu = false;
let store = {}

function setUwuState(a) {
	do_uwu = a;
}

function storeit(str, res) {
	store[str] = res;
}

function uwu(str, obj={
	stutter: true,
	wawr: false
}) {
	if (do_uwu) {
		if (str === "") {
			return str;
		}
		if (str.substring(0, 4) === "http") {
			return str; // don't screw with links
		}
		let inpstr = str;
		if (store[str]) {
			return store[str];
		}
		
		str = str
				 .toLowerCase()
				 .replace(/r/g, 'w')
				 .replace(/l/g, 'w')
				 .replace(/back/g, 'bakka')
		if (Math.random() < 0.05 && obj.wawr) {
			storeit(inpstr, "rawr");
			return "rawr";
		}
		if (Math.random() < 0.3 && obj.stutter) {
			str = str[0] + '-' + str;
		}
		if (Math.random() < 0.3) {
			if (!str[1] === '-' && obj.stutter) {
				str = str[0] + '-' + str;
			}
			if (str.endsWith('.')) {
				str = str.replace(/\./g, (Math.random()<0.5?' uwu':' OwO'))
			} else {
				str += Math.random()<0.5?' uwu': ' OwO'
			}
		}
		storeit(inpstr, str);
		return str
	} else {
		return str;
	}
}
function uwu2(str) {
	if (do_uwu) {
		return str.toLowerCase();
	}
	return str;
}
function owo(str) {
	if (do_uwu) {
		if (Math.random() < 0.3) {
			str = "owo:"
		}
	}
	return str;
}
function nowo(str, obj={
	stutter: true
}) {
	if (do_uwu) {
		if (store[str]) {
			return store[str];
		}
		if (str === "") {
			return str;
		}
		let inpstr = str;
		str = str
				 .toLowerCase()
				 .replace(/smudgedpasta/g, 'smudgedimpasta')
				 .replace(/r/g, 'w')
				 .replace(/l/g, 'w')
				 .replace(/back/g, 'bakka')
		if (Math.random() < 0.3 && obj.stutter) {
			str = str[0] + '-' + str;
		}
		storeit(inpstr, str);
		return str
	} else {
		return str;
	}
}
export {
	uwu,
	uwu2,
	owo,
	nowo,
	setUwuState
}
