import React from 'react'
import Message from '../message/message.js'
import Help from './help.json'
import { Link } from 'react-router-dom'
import md from '../markdown/md.js'
import {
	uwu,
	uwu2,
	owo,
	setUwuState
} from '../why.js'

const OFFLINE_DEV = true,
	MIZA_URL = "http://27.33.133.250:9801",
	MIZA_USERNAME = "Miza",
	PREFIX = "~"


export default class AtlasPage extends React.Component {
	constructor(props) {
		super();
		this.state = {
			searchTerm: '',
			hasFocused: false,
			showNSFW: !!localStorage.showNSFW,
			uwu: localStorage.uwu === "true"
		}
		setUwuState(this.state.uwu);
		this.searchRef = React.createRef();
		setTimeout(() => {
			this.searchRef && this.searchRef.current && !this.state.hasFocused && (() => {
				this.setState({
					hasFocused: true
				})
				this.searchRef.current.focus();
			})()
		}, 500)
	}
	search = (e) => {
		this.setState({
			searchTerm: e.target.value
		});
	}
	checkValues = (obj, checkstr) => {
		for (let key in obj) {
			if (typeof obj[key] === "object") {
				if (this.checkValues(obj[key], checkstr)) {
					return true;
				}
			}
			if (obj[key].toString().toLowerCase().includes(checkstr.toLowerCase())) {
				if (key === "usage" && checkstr === "?") {
					return false;
				}
				return true;
			}
		}
		return false;
	}
	prepStr = (str) => {
		// 8ball is _8ball in help for some reason, so /shrug
		return uwu2(str.replace(/‚ü®MIZA‚ü©/g, MIZA_USERNAME).replace(/‚ü®WEBSERVER‚ü©/g, MIZA_URL).replace("_8Ball", "8Ball"))
	}
	checkIsNsfw = (e) => {
		if (this.state.showNSFW) {
			return false;
		}
		if (typeof e === "string") {
			e = e.toLowerCase();
			return e === "nsfw";
		}
		if (typeof e === "object") {
			return (e.aliases && e.aliases.includes([
				'nsfw',
				'neko',
				'lewd'
			]))
		}
	
		return !this.state.showNSFW
	}
	render() {
		document.title = uwu("Miza Command Atlas")
		this.searchRef && this.searchRef.current && !this.state.hasFocused && (() => {
			this.setState({
				hasFocused: true
			})
			this.searchRef.current.focus();
		})()
		let navItems = [],
			fullItems = [],
			keys = 0
		for (let key in Help) {
			if (!this.checkValues(Help[key], this.state.searchTerm) &&  !key.toLowerCase().includes(this.state.searchTerm.toLowerCase())) {
				continue;
			}
			if (this.checkIsNsfw(key)) {
				continue;
			}
			navItems.push(<li
				className="sidebarCategory"
				key={keys++}
				><a
				href={"#" + encodeURIComponent(key.toLowerCase())}
			>{key}</a></li>)
			fullItems.push(<div
				id={key.toLowerCase()}
				className="categoryHead"
				key={keys++}
				><h1><a
					href={"#" + encodeURIComponent(key.toLowerCase())}
				>{uwu(key)}</a></h1></div>)	
				
			for (let e in Help[key]) {
			
				if (e.toLowerCase() === MIZA_USERNAME.toLowerCase()) {
					// is the auto-generated wack one
					continue;
				}
				if (this.checkIsNsfw(Help[key][e])) {
					continue;
				}
				if (!this.checkValues(Help[key][e], this.state.searchTerm) && !key.toLowerCase().includes(this.state.searchTerm.toLowerCase())) {
					continue;				
				}
				navItems.push(<li
					key={keys++}><a
					href={"#" + encodeURIComponent(e.toLowerCase())}
					id={"nav-"+ encodeURIComponent(e.toLowerCase())}
					className={document.location.hash.substring(1) === e.toLowerCase()?'active':null}
				>{this.prepStr(e)}</a></li>);
				fullItems.push(<div
					className={"command" + (document.location.hash.substring(1) === e.toLowerCase()?' active':'')}
					id={encodeURIComponent(e.toLowerCase())}
					key={keys++}
					><h2><a
						href={"#" + encodeURIComponent(e.toLowerCase())}
					>{this.prepStr(e)}</a></h2><h3
						className="aliases"
					>{Help[key][e].aliases.join(', ')}</h3><div className="markup">{
						md(uwu(this.prepStr(Help[key][e].description)))
					}</div><div className="markup usage">{owo(uwu2("Usage:"))}<br />{PREFIX}{uwu2(e)} {md(uwu2(Help[key][e].usage))}</div><Link 
						to={"run?command="+encodeURIComponent(e.toLowerCase())}
						className="run"
						>‚ñ∫</Link>
					</div>
				
				)
			}
		}
		
		return (<>
				<link rel="stylesheet" href="/mizatlas/page/atlas/atlas.css" type="text/css" />
				<nav className="sidebar">
					<ul>{navItems}</ul>
				</nav>
				<main>
					<h1>{uwu("Command atlas")}</h1>
					<h2>{uwu("Search by category or any command property.")}</h2><div
						className="labeledInput"><input 
						type="checkbox"
						id="showNSFW"
						checked={this.state.showNSFW}
						onChange={(e) => {
							this.setState({
								showNSFW: e.target.checked
							})
							localStorage.showNSFW = true
						}}
					/><label htmlFor="showNSFW">{uwu("Show NSFW")}</label></div><div
						className="labeledInput"><input 
						type="checkbox"
						id="uwuify"
						checked={this.state.uwu}
						onChange={(e) => {
							this.setState({
								uwu: e.target.checked
							})
							localStorage.uwu = e.target.checked
							setUwuState(e.target.checked);
						}}
					/><label htmlFor="uwuify">uwu</label></div>
					<Link
						to="."
						className="back"	
					>{uwu("Back to home")}</Link>
					<div className="searchHolder">
					<input 
						className="search"
						placeholder={uwu("Search commands...")}
						value={this.state.searchTerm}
						onChange={this.search}
						tabIndex="0"
						ref={this.searchRef}
					/></div>{fullItems}</main>
				</>)
	}
}
