import Embed from './embed/embed.js'
import MessageMeta from './meta.js'
import React from 'react'
import md from '../markdown/md.js'
import {
	uwu,
	nowo,
	setUwuState
} from '../why.js'

setUwuState(localStorage.uwu === "true");

function uwuMessage(msg) {
	if (!msg) {
		return msg;
	}
	if (msg.split) {
		for (let i = 0; i < msg.length; i++) {
			if (typeof msg[i] === "string") {
				msg[i] = nowo(msg[i], {
					stutter: false,
					wawr: false
				});
			} else {
				uwuMessage(msg[i]);
			}
		}
	} else if (typeof msg === "object") {
		for (let i in msg) {
			if (typeof msg[i] === "string") {
				msg[i] = nowo(msg[i], {
					stutter: false,
					wawr: false
				});
			} else {
				uwuMessage(msg[i]);
			}
		}
	} else if (typeof msg === "string") {
		msg = nowo(msg, {
			stutter: false,
			wawr: false
		});
	}
	return msg;
}


export default class Message extends React.Component {
	render() {
		let d = this.props.data
		if (!d || JSON.stringify(d) === "[]" || (!d.content && !d.embed && !d.embeds)) {
			return <></>
		}
		d.content = d.content ?? "";
		d = uwuMessage(d);
		let x = new Date()
		let y = `Today at ${x.getHours()%12}:${(x.getMinutes().toString().length < 2?"0" + x.getMinutes():x.getMinutes())} ${x.getHours()<12?"AM":"PM"}`
		let e = []
		d.embeds = d.embeds ?? [d.embed] ?? []
		for (let i = 0; i < d.embeds.length; i++) {
			if (!d.embeds[i]) {
				continue;
			}
			if (JSON.stringify(Object.keys(d.embeds[i])) === JSON.stringify(["url", "image"])) {
				e[e.length-1].images = e[e.length-1].images ?? [];
				e[e.length-1].images.push(d.embeds[i]);
				continue;
			}
			e.push(d.embeds[i]);
			e.images = [];
		}
		let f = [];
		for (let i = 0; i < e.length; i++) {
			f.push(<Embed
					data={e[i]}
				/>)
		}

		return (<div
			className="message cozyMessage groupStart wrapper cozy zalgo" 
			role="listitem" 
			tabIndex="-1" 
			style={this.props?.style}
			><MessageMeta
				avatar="/mizatlas/miza.png"
				name={uwu("Miza")}
				bot={true}
				time={uwu(y)}
			/>
			<div 
				className="contents" 
				role="document"
			><div 
				className="markup messageContent"
			>{md(d.content)}</div></div><div 
				className="container"
			>{f}</div></div>)
	}
}
